@import url("https://fonts.googleapis.com/css?family=Fira+Sans");

.inner-content {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

body {
  font-family: "Fira Sans",sans-serif;
  color: #f5f6fa;
  overflow: hidden;
}

.background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(#0C0E10, #446182);
}

.ground {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 25vh;
  background: #0C0E10;
}

.container {
  position: relative;
  margin: 0 auto;
  width: 85%;
  height: 100vh;
  padding-bottom: 25vh;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.left-section,
.right-section {
  position: relative;
}

.left-section {
  width: 40%;
}

.heading {
  text-align: center;
  font-size: 9em;
  line-height: 1.3em;
  margin: 2rem 0 0.5rem 0;
  padding: 0;
  text-shadow: 0 0 1rem #fefefe;
}

.subheading {
  text-align: center;
  max-width: 480px;
  font-size: 1.5em;
  line-height: 1.15em;
  padding: 0 1rem;
  margin: 0 auto;
}

.right-section {
  width: 50%;
}

.svgimg {
  position: absolute;
  bottom: 0;
  padding-top: 10vh;
  padding-left: 1vh;
  max-width: 100%;
  max-height: 100%;
}

.bench-legs {
  fill: #0C0E10;
}

.top-bench,
.bottom-bench {
  stroke: #0C0E10;
  stroke-width: 1px;
  fill: #5B3E2B;
}

.bottom-bench path:nth-child(1) {
  fill: #432d20;
}

.lamp-details {
  fill: #202425;
}

.lamp-accent {
  fill: #2c3133;
}

.lamp-bottom {
  fill: linear-gradient(#202425, #0C0E10);
}

.lamp-light {
  fill: #EFEFEF;
}

@keyframes glow {
  0% {
    text-shadow: 0 0 1rem #fefefe;
  }

  50% {
    text-shadow: 0 0 1.85rem #ededed;
  }

  100% {
    text-shadow: 0 0 1rem #fefefe;
  }
}

@media (max-width: 770px) {
  .ground {
    height: 0vh;
  }

  .container {
    flex-direction: column;
    padding-bottom: 0vh;
  }

  .left-section {
    width: 100%;
    height: 40%;
    position: absolute;
    top: 0;
  }

  .inner-content {
    position: relative;
    padding: 1rem 0;
  }

  .heading {
    font-size: 7em;
    line-height: 1.15;
    margin: 0;
  }

  .subheading {
    font-size: 1.3em;
    line-height: 1.15;
    max-width: 100%;
  }

  .right-section {
    width: 100%;
    height: 60%;
    position: absolute;
    bottom: 0;
  }

  .svgimg {
    padding: 0;
  }
}